import { Axios } from 'axios'
import {
  VaultLatestBlockRoutes,
  VaultLatestBlocksClientModel,
} from '../vault-latest-block.model'
import { VaultBlock, VaultBlocksSearchQuery } from '../../vault-blocks'
import { ApiEntity } from '../../core'

export class VaultLatestBlocksClient
  extends ApiEntity
  implements VaultLatestBlocksClientModel
{
  constructor(axios: Axios) {
    super(axios)
  }

  /**
   * Search latest blocks by params
   * @param searchParams - the filters to apply
   * @returns a blocks page
   */
  public search(searchParams?: VaultBlocksSearchQuery) {
    return this._search<VaultBlock>(
      VaultLatestBlockRoutes.v1Search,
      searchParams
    )
  }

  /**
   * List latest blocks by params
   * @param searchParams - the filters to apply
   * @returns the block list
   */
  public list(searchParams?: VaultBlocksSearchQuery) {
    return this._list<VaultBlock>(VaultLatestBlockRoutes.v1Search, searchParams)
  }

  /**
   * Read a latest block by params
   * @param searchParams - the filters to apply
   * @returns the matching block
   */
  public readOne(searchParams: VaultBlocksSearchQuery) {
    return this._readOne<VaultBlock>(
      VaultLatestBlockRoutes.v1Search,
      searchParams
    )
  }

  /**
   * Find a latest block by params
   * @param searchParams - the filters to apply
   * @returns the matching block
   */
  public findOne(searchParams: VaultBlocksSearchQuery) {
    return this._findOne<VaultBlock>(
      VaultLatestBlockRoutes.v1Search,
      searchParams
    )
  }
}
